import React, { useEffect, useState } from "react";
import Loader from "../../../common/loader/Loader";
import { Select as AntSelect, message } from "antd";
import {
  getRetailersForASMZSM,
  getAepsReportforASMASM,
  getAepsExcelReportforASMASM,
  enquiryAepsTransferforASMASM,
} from "../../../api/login/Login";
import {
  Pagination,
  Autocomplete,
  Box,
  TextField,
  Typography,
  FormControl,
  InputLabel,
  MenuItem,
  Select,
} from "@mui/material";
import { Button } from "react-bootstrap";
import { Link, useLocation } from "react-router-dom";
import * as XLSX from "xlsx";
import { useReportDates } from "../../../utils/getDate";
const AsmAEPSReport = ({ title }) => {
  const today = new Date().toISOString().split("T")[0];
  const [loading, setLoading] = useState(false);
  const [selectedRow, setSelectedRow] = useState(null);
  const [page, setPage] = useState(1);
  const [retailers, setRetailers] = useState([]);
  const [selectedRetailer, setSelectedRetailer] = useState(null);
  const [totalCount, setTotalCount] = useState(0);
  const [submit, setSubmit] = useState(false);
  const [data, setData] = useState([]);

  const [rowsPerPage, setRowsPerPage] = useState(10);

  const [filter, setFilter] = useState({
    customer_mobile: "",
    response: "",
    start_date: "",
    end_date: "",
    txn_id: "",
    type: "",
    bank: "",
    addhar_no: "",
  });

  const statusOptions = [
    { value: "", label: "All" },
    { value: "1", label: "Pending" },
    { value: "2", label: "Success" },
    { value: "3", label: "Failed" },
    { value: "4", label: "Refunded" },
  ];

  //   const bankOptions = [
  //     { value: "", label: "All" },
  //     { value: "aeps1", label: "AEPS 1" },
  //   ];

  const typeTypeOptions = [
    { value: "", label: "All" },
    { value: "CW", label: "Cash Withdraw" },
    { value: "BE", label: "Balance Enquiry" },
    { value: "MS", label: "Mini Statement" },
    { value: "CD", label: "Cash Deposit" },
  ];

  const getRetailers = async () => {
    try {
      let res = await getRetailersForASMZSM();
      if (res?.data?.error === false) {
        setRetailers(res.data?.data || []);
      } else {
        setRetailers([]);
      }
    } catch (err) {
      console.error(err);
      setRetailers([]);
    }
  };

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFilter((prev) => ({
      ...prev,
      [name]: value,
    }));
  };

  const handleStatusChange = (value, key) => {
    setFilter((prev) => ({
      ...prev,
      [key]: value,
    }));
  };

  const handleSearch = () => {
    if (filter.customer_mobile && filter.customer_mobile.length !== 10) {
      message.error("Please enter a valid 10-digit customer mobile number");
      return;
    }

    if (filter.addhar_no && filter.addhar_no.length !== 4) {
      message.error("Please enter last 4 digits of Aadhaar number");
      return;
    }

    if (
      (filter.start_date && !filter.end_date) ||
      (!filter.start_date && filter.end_date)
    ) {
      message.error("Please select both Start Date and End Date");
      return;
    }

    setPage(1);
    setSubmit((prev) => !prev);
  };

  const ResetData = () => {
    setFilter({
      customer_mobile: "",
      response: "",
      start_date: "",
      end_date: "",
      txn_id: "",
      type: "",
      bank: "",
      addhar_no: "",
    });
    setSelectedRetailer(null);
    setSubmit((prev) => !prev);
    setRowsPerPage(10);
    setPage(1);
  };

  const handleChangeRowsPerPage = (e) => {
    setRowsPerPage(e.target.value);
    setPage(1);
  };

  const handleChangePage = (event, value) => {
    setPage(value);
  };

  //   const handleViewClick = (item) => {
  //     setSelectedRow(item);
  //   };

  useEffect(() => {
    getRetailers();
  }, []);

  const getData = async () => {
    setLoading(true);
    try {
      const res = await getAepsReportforASMASM({
        ...filter,
        retailer_id: selectedRetailer?._id || "",
        page,
        limit: rowsPerPage,
      });
      if (res?.data?.error === false) {
        setData(res.data?.data?.aepsAllTxns || []);
        setTotalCount(res.data?.data?.totalTxns || 0);
      } else {
        setData([]);
        setTotalCount(0);
        message.error(res.data?.message || "Something went wrong");
      }
    } catch (error) {
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    getData();
  }, [submit, page, rowsPerPage]);

  const responseEnquiry = async (id) => {
    setLoading(true);
    try {
      let res = await enquiryAepsTransferforASMASM(id);
      if (res?.data?.error === false) {
        message.success(res.data?.message);
        setSubmit((prev) => !prev);
      } else {
        message.error(res.data?.message);
      }
    } catch (error) {
    } finally {
      setLoading(false);
    }
  };

  const downloadExcel = async () => {
    try {
      setLoading(true);
      const res = await getAepsExcelReportforASMASM({
        ...filter,
        retailer_id: selectedRetailer?._id || "",
      });

      if (res?.data?.data?.aepsAllTxns?.length > 0) {
        const dataForExcel = res?.data?.data?.aepsAllTxns?.map((item, i) => ({
          "Sr. No": i + 1,
          "Date / Time": item?.createdAt || "",
          "Transaction ID": item?.txn_id || "",
          "Bank RRN": item?.bank_rrn || "",
          "Bank Name": item?.bank_name || "",
          Amount: item?.amount || "",
          "Balance Amount": item?.bal_amount || "",
          "Opening Balance": item?.opening_balance || "",
          "Closing Balance": item?.closing_balance || "",
          "Transaction Type":
            typeTypeOptions?.find((val) => val?.value === item?.type)?.label ||
            item?.type ||
            "",
          Status:
            item?.response === 2
              ? "Success"
              : item?.response === 1
                ? "Pending"
                : item?.response === 3
                  ? "Failed"
                  : item?.response === 4
                    ? "Refund"
                    : "Unknown",
          Message: item?.message || "",
          "Aadhaar Number": item?.adhaar_no || "",
          "Last 4 Aadhaar": item?.last_adhar || "",
          "Customer Mobile": item?.customer_mobile || "",
          // "Bank IIN": item?.nationalbankidentification || "",
          "Retailer Name": item?.retailer_name || "",
          "Retailer Mobile": item?.retailer_mobile || "",
          "Retailer Refer ID": item?.retailer_refer_id || "",
          "Created By Name": item?.CreatedBy_name || "",
          "Created By Mobile": item?.CreatedBy_mobile || "",
          "Created By Refer ID": item?.CreatedBy_refer_id || "",
          "Status Update Time": item?.status_update_time || "",
        }));

        const ws = XLSX.utils.json_to_sheet(dataForExcel);
        const wb = XLSX.utils.book_new();
        XLSX.utils.book_append_sheet(wb, ws, "AEPS Report");
        XLSX.writeFile(
          wb,
          `AEPS_Report_${new Date().toLocaleDateString()}.xlsx`,
        );
      } else {
        message.error("No data available to export!");
      }
    } catch (error) {
      message.error("Failed to download Excel. Please try again.");
    } finally {
      setLoading(false);
    }
  };

  const totalPages = Math.ceil(totalCount / rowsPerPage);

  return (
    <>
      {loading && <Loader />}

      {title && (
        <h3
          className="mt-3 mb-3"
          style={{
            color: "var(--bs-primary2)",
            fontWeight: 700,
            borderBottom: "1px solid var(--bs-primary2)",
            paddingBottom: "8px",
          }}
        >
          {title}
        </h3>
      )}
      <div className="card">
        <div className="card-body">
          <form>
            <div className="form-row" style={{ alignItems: "end" }}>
              <div className="form-group col-md-4">
                <label>Start Date</label>
                <input
                  type="date"
                  name="start_date"
                  className="form-control input-white-bg"
                  max={today}
                  value={filter.start_date}
                  onChange={handleChange}
                />
              </div>
              <div className="form-group col-md-4">
                <label>End Date</label>
                <input
                  type="date"
                  name="end_date"
                  className="form-control input-white-bg"
                  min={filter.start_date}
                  max={today}
                  value={filter.end_date}
                  onChange={handleChange}
                />
              </div>
              {/* <div className="form-group col-md-4">
                <label>Customer Mobile</label>
                <input
                  type="text"
                  name="customer_mobile"
                  className="form-control bs-autofill-fix"
                  placeholder="Enter Customer Mobile"
                  value={filter.customer_mobile}
                  maxLength={10}
                  onChange={(e) => {
                    const value = e.target.value.replace(/\D/g, "");
                    setFilter((prev) => ({
                      ...prev,
                      customer_mobile: value,
                    }));
                  }}
                  onWheel={(e) => e.target.blur()}
                />
              </div> */}
              <div className="form-group col-md-4">
                <label>Aadhaar Number</label>
                <input
                  type="text"
                  name="addhar_no"
                  className="form-control bs-autofill-fix primary-theme-input"
                  placeholder="Enter Last 4 Aadhaar Digits"
                  value={filter.addhar_no}
                  maxLength={4}
                  onChange={(e) => {
                    const value = e.target.value.replace(/\D/g, "");
                    setFilter((prev) => ({
                      ...prev,
                      addhar_no: value,
                    }));
                  }}
                  onWheel={(e) => e.target.blur()}
                />
              </div>
              <div className="form-group col-md-4">
                <label>Txn Id</label>
                <input
                  type="text"
                  name="txn_id"
                  className="form-control bs-autofill-fix primary-theme-input"
                  placeholder="Enter Txn Id"
                  value={filter.txn_id}
                  onChange={handleChange}
                />
              </div>
              {/* <div className="form-group col-md-4">
                <label>Batch Id</label>
                <input
                  type="text"
                  name="batch_id"
                  className="form-control"
                  placeholder="Enter Batch Id"
                  value={filter.batch_id}
                  onChange={handleChange}
                />
              </div> */}
              <div className="form-group col-md-4">
                <label>Type</label>
                <AntSelect
                  style={{ width: "100%" }}
                  value={filter.type}
                  onChange={(value) => handleStatusChange(value, "type")}
                  options={typeTypeOptions}
                />
              </div>
              <div className="form-group col-md-4">
                <label>Status</label>
                <AntSelect
                  style={{ width: "100%" }}
                  value={filter.response}
                  onChange={(value) => handleStatusChange(value, "response")}
                  options={statusOptions}
                />
              </div>
              <div className="form-group col-md-4">
                <label>Select Retailer</label>
                <Autocomplete
                  options={retailers}
                  getOptionLabel={(option) => option.name || ""}
                  value={selectedRetailer ?? null}
                  size="small"
                  className="input-white-bg"
                  onChange={(e, newVal) => setSelectedRetailer(newVal)}
                  renderOption={(props, option) => (
                    <li {...props} key={option.retailer_id}>
                      <Box sx={{ fontSize: "13px" }}>{option.name}</Box>
                    </li>
                  )}
                  renderInput={(params) => (
                    <TextField
                      {...params}
                      label="Select Retailer"
                      InputLabelProps={{
                        sx: {
                          "&.MuiInputLabel-root:not(.MuiInputLabel-shrink)": {
                            top: "-3px",
                          },
                        },
                      }}
                      sx={{
                        "& label.Mui-focused": {
                          color: "#8b8b8bff",
                        },
                        "& .MuiInputBase-root": { backgroundColor: "#fff" },
                        "& .MuiOutlinedInput-root": {
                          "&:hover fieldset": {
                            borderColor: "#ced4da",
                          },
                          "&.Mui-focused fieldset": {
                            borderColor: "#ced4da",
                          },
                        },
                      }}
                    />
                  )}
                />
              </div>
              <div className="form-group col-md-12">
                <button
                  type="button"
                  className="btn mr-3 text-white"
                  style={{ backgroundColor: "#6C757D" }}
                  onClick={handleSearch}
                >
                  Search
                </button>
                <button
                  type="button"
                  className="btn btn-light"
                  style={{ backgroundColor: "#F8F9FA" }}
                  onClick={ResetData}
                >
                  Reset
                </button>
              </div>
            </div>
          </form>
        </div>
      </div>
      <div className="card mt-4" style={{ overflow: "auto" }}>
        <div className="card-body">
          <div id="myTable_wrapper" className="dataTables_wrapper no-footer">
            <div className="d-flex  justify-content-end align-items-center">
              <div className="dataTables_length mb-3" id="myTable_length">
                <Button
                  className="btn btn-success cusxel"
                  onClick={downloadExcel}
                  // disabled={loading}
                >
                  Download Excel sheet
                </Button>
              </div>
            </div>

            <div
              className="table-responsive"
              style={{
                maxHeight: "60vh",
                overflow: "auto",
                borderRadius: "10px",
                border: "1px solid #eaeaea",
              }}
            >
              <table
                className="table table-hover table-bordered mb-0"
                style={{ whiteSpace: "nowrap" }}
              >
                <thead
                  style={{
                    position: "sticky",
                    top: 0,
                    zIndex: 2,
                    background: "#f8f9fa",
                  }}
                >
                  <tr role="row">
                    <th className="sorting">S.No</th>
                    <th className="sorting">Txn Date</th>
                    {/* <th className="sorting">Batch Id</th> */}
                    <th className="sorting">Txn Id</th>
                    <th className="sorting">Aadhaar Number </th>
                    <th className="sorting">Bank Name</th>
                    <th className="sorting">Opening Balance</th>
                    <th className="sorting">Amount</th>
                    <th className="sorting">Closing Balance</th>
                    <th className="sorting">Txn Status</th>

                    <th className="sorting">Transaction Type</th>
                    <th className="sorting">Retailer Name</th>
                    <th className="sorting">Retailer Code</th>
                    <th className="sorting">Retailer Number</th>
                    <th className="sorting">Status Update At</th>
                    <th className="sorting">Enquiry</th>
                    <th className="sorting">Invoice</th>
                    <th className="sorting">Dispute</th>
                  </tr>
                </thead>
                <tbody>
                  {data?.map((item, i) => (
                    <tr className="odd" key={item?._id}>
                      <td valign="top" className="dataTables_empty">
                        {i + 1 + (page - 1) * rowsPerPage}
                      </td>
                      <td valign="top" className="dataTables_empty">
                        {item?.createdAt}
                      </td>
                      {/* <td valign="top" className="dataTables_empty">
                        {item?.batchId || ''}
                      </td> */}
                      <td valign="top" className="dataTables_empty">
                        {item?.txn_id || "---"}
                      </td>
                      <td valign="top" className="dataTables_empty">
                        ********{item?.last_adhar}
                      </td>
                      <td valign="top" className="dataTables_empty">
                        {item?.bank_name}
                      </td>
                      <td valign="top" className="dataTables_empty">
                        {item?.opening_balance}
                      </td>
                      <td valign="top" className="dataTables_empty">
                        {item?.amount}
                      </td>
                      <td valign="top" className="dataTables_empty">
                        {item?.closing_balance}
                      </td>
                      <td valign="top" className="dataTables_empty">
                        {item?.response == 1 ? (
                          <button
                            className="btn btn-warning"
                            style={{ width: "130px" }}
                          >
                            Pending
                          </button>
                        ) : (
                          ""
                        )}
                        {item?.response == 2 ? (
                          <button
                            className="btn btn-success"
                            style={{ width: "130px" }}
                          >
                            Success
                          </button>
                        ) : (
                          ""
                        )}
                        {item?.response == 3 ? (
                          <button
                            className="btn btn-danger"
                            style={{ width: "130px" }}
                          >
                            Failed
                          </button>
                        ) : (
                          ""
                        )}
                        {item?.response == 4 ? (
                          <button
                            className="btn btn-light"
                            style={{ width: "130px" }}
                          >
                            Refunded
                          </button>
                        ) : (
                          ""
                        )}
                        {item?.response == 5 ? (
                          <button
                            className="btn btn-light"
                            style={{ width: "130px" }}
                          >
                            Not Initiated
                          </button>
                        ) : (
                          ""
                        )}
                      </td>
                      {/* <td valign="top" className="dataTables_empty text-center">
                        <FaEye
                          className="eyeiconreport"
                          onClick={() => handleViewClick(item)}
                        />
                      </td> */}

                      <td valign="top" className="dataTables_empty">
                        {
                          typeTypeOptions?.find(
                            (val) => val?.value === item?.type,
                          )?.label
                        }
                      </td>
                      <td valign="top" className="dataTables_empty">
                        {item?.retailer_name}
                      </td>
                      <td valign="top" className="dataTables_empty">
                        {item?.retailer_refer_id}
                      </td>
                      <td valign="top" className="dataTables_empty">
                        {item?.retailer_mobile}
                      </td>
                      <td valign="top" className="dataTables_empty">
                        {item?.status_update_time}
                      </td>
                      <td valign="top" className="dataTables_empty">
                        <Button
                          onClick={() => responseEnquiry(item?._id)}
                          disabled={item?.response !== 1 || loading}
                          className="btn-enquiry"
                        >
                          Enquiry
                        </Button>
                      </td>
                      <td valign="top" className="dataTables_empty">
                        <Link
                          className="btn btn-primary btn-invoice"
                          to={`/aeps-invoice/${item?._id}`}
                        >
                          Invoice
                        </Link>
                      </td>
                      <td valign="top" className="dataTables_empty">
                        <Link
                          className="btn btn-primary btn-dispute"
                          to={`/add-ticket/${item?._id}`}
                          state={{
                            item,
                            serviceId: `${item?._id}`,
                          }}
                        >
                          <span style={{ color: "var(--bs-primary2)" }}>Dispute</span>
                        </Link>
                      </td>
                    </tr>
                  ))}

                  {data?.length === 0 && (
                    <td
                      style={{ textAlign: "center" }}
                      colSpan={17}
                      valign="top"
                      className="dataTables_empty"
                    >
                      No Record Found
                    </td>
                  )}
                </tbody>
              </table>
            </div>
          </div>
          {/* </div> */}
          <Box
            display="flex"
            justifyContent="space-between"
            alignItems="center"
            mt={2}
            flexWrap="wrap"
            gap={2}
          >
            <Box display="flex" alignItems="center" gap={2}>
              <FormControl size="small" sx={{ width: "150px" }}>
                <InputLabel>Rows per page</InputLabel>
                <Select
                  value={rowsPerPage}
                  onChange={handleChangeRowsPerPage}
                  label="Rows per page"
                  sx={{
                    fontSize: "14px",
                    "& .MuiOutlinedInput-notchedOutline": {
                      borderColor: "#ccc",
                    },
                    "&:hover .MuiOutlinedInput-notchedOutline": {
                      borderColor: "var(--bs-primary1)",
                    },
                    "&.Mui-focused .MuiOutlinedInput-notchedOutline": {
                      borderColor: "var(--bs-primary1)",
                    },
                  }}
                >
                  {[5, 10, 25, 50].map((num) => (
                    <MenuItem key={num} value={num}>
                      {num}
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>
              <Typography
                variant="body2"
                sx={{ fontSize: "14px", color: "#555" }}
              >
                Total Entries: {totalCount}
              </Typography>
            </Box>
            <Pagination
              count={totalPages}
              page={page}
              onChange={handleChangePage}
              shape="rounded"
              showFirstButton
              showLastButton
              sx={{
                "& .MuiPaginationItem-root": {
                  color: "var(--bs-primary1)",
                  borderRadius: "8px",
                },
                "& .MuiPaginationItem-root.Mui-selected": {
                  backgroundColor: "var(--bs-primary1)",
                  color: "#fff",
                  fontWeight: "bold",
                  "&:hover": { backgroundColor: "var(--bs-primary1)" },
                },
              }}
            />
          </Box>
        </div>
      </div>
    </>
  );
};

export default AsmAEPSReport;
